body {
  @import url('https://fonts.googleapis.com/css2?family=EB+Garamond&family=Inter:wght@100&display=swap');
  font-family: 'Inter', sans-serif;
}
.total {
  display: flex;
  flex-direction: column;
}
.all {
  width: 100%;
  max-width: 1440px;
  align-self: center;
  position: relative;
  overflow: hidden;
  /* display: grid; */

}
.blockone {
  min-height: 870px;
  position: relative;
  background-image: url(./img/Background.png);
}

.logo {
  height: 32px;
  width: 42px;
  display: flex;
  padding-left: 20px;
  padding-right: 20px;
  align-items: center;
}
.logo:nth-child(2) {
  padding-top: 5px;
  font-size: 20px;
  font-weight: bold;
}


.header {
  display: flex;
  justify-content: space-between;
  height: 50px;
  align-items: center;

}
.app {
  justify-content: flex-end;
  margin-right: 270px;
}
.headtext {
  padding-right: 450px;
  padding-top: 8px;
  margin-left: 50px;
  font-weight: bold;
  cursor: pointer;
}
.headtext > span {
  margin-left: 15px;
}
.ends {
  display: flex;
  cursor: pointer;
}
.apps {
  opacity: 0;

}
.newtext {
  opacity: 0;
}
.menu {
  opacity: 3;
}
@media screen and (min-width: 376px) {
  .apps {
      opacity: 3;
      width: 285px;
      height: 45px;
  }
  .menu {
    opacity: 3;
  }
}
@media screen and (min-width: 769px) {
  .newtext {
      opacity: 3;
      margin-left: 20px;
  }
  .newtext>span {
    padding-left: 20px;
    font-weight: bold;
    font-size: 15px;
  }
  .menu {
    opacity: 0;
  }
}

